.form-container{
    margin: 4rem 6rem;
    color: #2a2a2a;
}
.form-container form{
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;
}

.form-container input{
    height: 3rem;
    padding: 0 3rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border: 1px solid #2a2a2a;
}
.form-container textarea{
    padding: 1rem 3rem;
    font-family: poppins;
    width: 100%;
}

.form-container button{
    display: inline;
    margin-top: 1rem;
    background-color: black;
    color: white;
}

@media screen and (max-width){
    .form-container{
        margin: 4rem 1rem;

    }
    .form-container form{
        padding-top: 3rem;
         width: 90%;

    }
}